import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  Button,
  Checkbox,
  Container,
  FormControl,
  FormControlLabel,
  FormGroup,
  Grid,
  InputAdornment,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography,
  Card,
  CardContent,
  Paper,
} from "@mui/material";

function UserDetails() {
  const username = sessionStorage.getItem("username");
  const teamName = sessionStorage.getItem("username");
  const [cornuser1, setCornuser1] = useState([]);
  useEffect(() => {
    // Fetch users for the Corn crop from the API
    axios
      .post("http://18.225.175.93:80/api/get_corn_user", {
        teamName: teamName, // Send 'teamName' in the request body
      })
      .then((response) => {
        setCornuser1(response.data);
        console.log(response.data);
        console.log(cornuser1);
      })
      .catch((error) => {
        console.error("Error fetching Corn users data:", error);
      });
  }, []);

  return (
    <Container>
      <h2>Welcome, {username}!</h2>
      <h4>User Details</h4>
      <p>Username: {cornuser1[0].teamName}</p>
      {/* Change to teamName */}
      <p>Capitan First Name: {cornuser1[0].captainFirstName}</p>
      {/* Change to captainFirstName */}
      <p>Capitan Last Name: {cornuser1[0].captainLastName}</p>
      {/* Change to captainLastName */}
      <p>Address 1: {cornuser1[0].address1}</p>
      <p>Address 2: {cornuser1[0].address2}</p>
      <p>City: {cornuser1[0].city}</p>
      <p>State: {cornuser1[0].state}</p>
      <p>Zip Code: {cornuser1[0].zipCode}</p>
      <p>Country: {cornuser1[0].country}</p>
      <p>Email: {cornuser1[0].email}</p> {/* Change to email */}
      <p>Phone: {cornuser1[0].phone}</p> {/* Change to phone */}
      {/* Display other user details here */}
    </Container>
  );
}
export default UserDetails;
