import React, { useState, useEffect } from "react";
import {
  Button,
  Container,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  Card,
  CardContent,
} from "@mui/material";
import axios from "axios"; // Import Axios
import HighlightOffIcon from "@mui/icons-material/HighlightOff";
import DoneIcon from "@mui/icons-material/Done";

function MarketingOptionsForm() {
  const [contractType, setContractType] = useState("");
  const [quantityBushels, setQuantityBushels] = useState("");
  const [marketingOptions, setMarketingOptions] = useState([]);
  const [dateToday, setDateToday] = useState(new Date().toISOString());
  const teamName = sessionStorage.getItem("username");

  // Function to handle card click and update the selected contract type
  const handleCardClick = (value) => {
    setContractType(value);
  };

  const bushels = [
    "10k",
    "20k",
    "30k",
    "40k",
    "50k",
    "60k",
    "70k",
    "80k",
    "90k",
    "100k",
  ];

  const handleQuantityCardClick = (selectedLevel) => {
    if (selectedLevel == quantityBushels) setQuantityBushels("None");
    else setQuantityBushels(selectedLevel);
  };

  useEffect(() => {
    // Fetch data from the backend when the component mounts
    fetchDataFromBackend();
  }, []);

  const handleAddOption = () => {
    // Prepare the data to be sent
    const newOption = {
      date: dateToday,
      contractType,
      quantityBushels,
      teamName,
      complete: "no",
    };

    // Send a POST request to your backend endpoint to insert the new option
    axios
      .post("http://18.225.175.93:80/api/insertMarketingOption", newOption) // Update the URL accordingly
      .then((response) => {
        // Handle the successful response if needed
        if (response.status === 200) {
          console.log("Application data sent to the backend successfully");
          fetchDataFromBackend();
        } else {
          console.error("Failed to send application data to the backend");
        }

        // Clear the form fields
        setContractType("");
        setQuantityBushels("");

        // Fetch the updated data from the backend
      })
      .catch((error) => {
        // Handle errors
        console.error("Error adding option:", error);
      });
  };

  const fetchDataFromBackend = () => {
    // Make an API request to fetch data from the backend
    axios
      .get("http://18.225.175.93:80/api/fetchMarketingOptions", {
        params: {
          teamName, // Replace with the actual team name
        },
      }) // Update the URL accordingly
      .then((response) => {
        if (response.status === 200) {
          // Set the retrieved data to the state variable
          setMarketingOptions(response.data);
        } else {
          console.error("Failed to fetch data from the backend");
        }
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
  };

  return (
    <Container maxWidth="100%">
      <Typography variant="h6" gutterBottom>
        Marketing Options
      </Typography>
      <div className="field">
        <div className="control">
          <div style={{ display: "flex" }}>
            {/* Cards for selecting contract type */}
            <Card
              onClick={() => handleCardClick("Flat-price")}
              sx={{
                cursor: "pointer",
                padding: "10px",
                backgroundColor:
                  contractType === "Flat-price" ? "#fa4616" : "#D8D4D7",
                border: "2px solid #fa4616", // Highlight the selected card
                marginRight: "30px", // Add margin to separate cards
              }}
            >
              <CardContent>
                <Typography variant="body2">Flat-price Contract</Typography>
              </CardContent>
            </Card>
            <Card
              onClick={() => handleCardClick("Basis")}
              sx={{
                cursor: "pointer",
                padding: "10px",
                backgroundColor:
                  contractType === "Basis" ? "#fa4616" : "#D8D4D7", // Highlight the selected card
                border: "2px solid #fa4616",
              }}
            >
              <CardContent>
                <Typography variant="body2">Basis Contract</Typography>
              </CardContent>
            </Card>
          </div>
          <p></p>
          <FormControl variant="outlined" fullWidth>
            <InputLabel htmlFor="select-contract-type">
              Contract Type
            </InputLabel>
            <Select
              id="select-contract-type"
              name="contract-type"
              label="Contract Type"
              value={contractType}
              onChange={(e) => setContractType(e.target.value)}
            >
              <MenuItem value="Flat-price">Flat-price</MenuItem>
              <MenuItem value="Basis">Basis</MenuItem>
            </Select>
          </FormControl>
        </div>
        <br></br>

        <div style={{ display: "flex", flexWrap: "wrap" }}>
          {/* Cards for selecting amount */}
          <Card
            sx={{
              cursor: "pointer",
              padding: "8px",
              backgroundColor: "white",
              marginBottom: "8px",
              marginRight: "2px",
              textAlign: "center",
              fontWeight: "bold",
              //border: "2px solid #fa4616",
            }}
          >
            <CardContent>
              <Typography variant="body2">Quantity</Typography>
            </CardContent>
          </Card>

          {bushels.map((option) => (
            <Card
              key={option}
              onClick={() => handleQuantityCardClick(option)}
              sx={{
                cursor: "pointer",
                padding: "8px",
                backgroundColor:
                  quantityBushels === option ? "#fa4616" : "#D8D4D7",
                marginBottom: "8px",
                marginRight: "2px",
                textAlign: "center",
                fontWeight: "bold",
                border: "2px solid #fa4616",
              }}
            >
              <CardContent>
                <Typography variant="body2">{option}</Typography>
              </CardContent>
            </Card>
          ))}
        </div>
        <br></br>

        <div className="control">
          <FormControl variant="outlined" fullWidth>
            <InputLabel htmlFor="select-quantity-bushels">
              Quantity Bushels
            </InputLabel>
            <Select
              id="select-quantity-bushels"
              name="quantity-bushels"
              label="Quantity Bushels"
              value={quantityBushels}
              onChange={(e) => setQuantityBushels(e.target.value)}
            >
              <MenuItem value="None">None</MenuItem>
              <MenuItem value="10k">10k</MenuItem>
              <MenuItem value="20k">20k</MenuItem>
              <MenuItem value="30k">30k</MenuItem>
              <MenuItem value="40k">40k</MenuItem>
              <MenuItem value="50k">50k</MenuItem>
              <MenuItem value="60k">60k</MenuItem>
              <MenuItem value="70k">70k</MenuItem>
              <MenuItem value="80k">80k</MenuItem>
              <MenuItem value="90k">90k</MenuItem>
              <MenuItem value="100k">100k</MenuItem>
            </Select>
          </FormControl>
        </div>
      </div>
      <br></br>
      <div className="field">
        <div className="control">
          <Button
            id="marketing-option-add"
            variant="outlined"
            color="primary"
            onClick={handleAddOption}
          >
            Add Option
          </Button>
        </div>
      </div>
      <div className="table-container">
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>#</TableCell>
                <TableCell>Date (EST)</TableCell>
                <TableCell>Contract Type</TableCell>
                <TableCell>Quantity Bushels</TableCell>
                <TableCell>Complete</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {marketingOptions.map((option, index) => (
                <TableRow key={index}>
                  <TableCell>{index + 1}</TableCell>
                  <TableCell>{option.date}</TableCell>
                  <TableCell>{option.contractType}</TableCell>
                  <TableCell>{option.quantityBushels}</TableCell>
                  {/* <TableCell>{option.complete}</TableCell> */}
                  <TableCell>
                    {option.complete === "no" ? (
                      <HighlightOffIcon />
                    ) : (
                      <>
                        <DoneIcon />
                        {option.complete}
                      </>
                    )}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    </Container>
  );
}

export default MarketingOptionsForm;
