import React, { useState, useEffect } from "react";
import {
  Button,
  Container,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Typography,
  TextField,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Card,
  CardContent,
} from "@mui/material";
import axios from "axios";

function CottonGrowthRegulationForm() {
  const [regulator, setRegulator] = useState("");
  const [rate, setRate] = useState("");
  const [formData, setFormData] = useState([]);
  const teamName = sessionStorage.getItem("username");

  const handleCardClick = (value) => {
    setRegulator(value);
  };

  // Fetch existing growth regulator data for the user
  const fetchGrowthRegulatorData = () => {
    axios
      .get("http://18.225.175.93:80/api/cottonFetchGrowthRegulation", {
        params: { teamName: teamName },
      })
      .then((response) => {
        if (response.status === 200) {
          setFormData(response.data);
        } else {
          console.error("Failed to fetch data from the backend");
        }
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
  };

  // Fetch existing growth regulator data for the user when the component mounts
  useEffect(() => {
    fetchGrowthRegulatorData();
  }, [teamName]);

  const handleSubmit = async () => {
    try {
      // Prepare the data to be sent
      const formDataToSubmit = {
        teamName: teamName,
        date: new Date().toISOString(), // Assuming you want to capture the date
        regulator: regulator,
        rate: rate,
      };

      // Send a POST request to your backend endpoint
      const response = await axios.post(
        "http://18.225.175.93:80/api/cottonInsertGrowthRegulation",
        formDataToSubmit
      );

      if (response.status === 200) {
        console.log("Data sent to the backend successfully");
        // Clear the form fields
        setRegulator("");
        setRate("");
        // Fetch updated data after submission
        fetchGrowthRegulatorData();
      } else {
        console.error("Failed to send data to the backend");
      }
    } catch (error) {
      console.error("Error submitting form:", error);
    }
  };

  return (
    <Container>
      <Typography variant="h6" gutterBottom>
        Growth Regulator
      </Typography>
      <br></br>
      <div className="field">
        <div className="block control">
          <div style={{ display: "flex" }}>
            {/* Cards for selecting contract type */}
            <Card
              onClick={() => handleCardClick("Pix (Mepiquat Chloride)")}
              sx={{
                cursor: "pointer",
                padding: "10px",
                backgroundColor:
                  regulator === "Pix (Mepiquat Chloride)"
                    ? "#fa4616"
                    : "#D8D4D7",
                border: "2px solid #fa4616", // Highlight the selected card
                marginRight: "30px", // Add margin to separate cards
              }}
            >
              <CardContent>
                <Typography variant="body2">Pix (Mepiquat Chloride)</Typography>
              </CardContent>
            </Card>
            <Card
              onClick={() => handleCardClick("Stance")}
              sx={{
                cursor: "pointer",
                padding: "10px",
                backgroundColor: regulator === "Stance" ? "#fa4616" : "#D8D4D7", // Highlight the selected card
                border: "2px solid #fa4616",
              }}
            >
              <CardContent>
                <Typography variant="body2">Stance</Typography>
              </CardContent>
            </Card>
          </div>
          <p></p>

          <FormControl variant="outlined" fullWidth>
            <InputLabel htmlFor="select-regulator">Regulator</InputLabel>
            <Select
              id="select-regulator"
              name="regulator"
              label="Regulator"
              value={regulator}
              onChange={(e) => setRegulator(e.target.value)}
            >
              <MenuItem value="Pix (Mepiquat Chloride)">
                Pix (Mepiquat Chloride)
              </MenuItem>
              <MenuItem value="Stance">Stance</MenuItem>
            </Select>
          </FormControl>
        </div>
        <br />
        <div className="block control">
          <TextField
            id="rate"
            label="Rate (any)"
            variant="outlined"
            fullWidth
            value={rate}
            onChange={(e) => setRate(e.target.value)}
          />
        </div>
        <br></br>
        <div className="columns">
          <div className="column">
            <div className="field">
              <div className="control">
                <Button
                  id="regulation-selection-submit"
                  variant="contained"
                  color="primary"
                  onClick={handleSubmit}
                >
                  Submit
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* Existing Growth Regulator Data */}
      <div>
        <Typography variant="h6" gutterBottom>
          Existing Growth Regulator Data
        </Typography>
        <TableContainer component={Paper}>
          <Table aria-label="existing-growth-regulator-data">
            <TableHead>
              <TableRow>
                {/* <TableCell>Team Name</TableCell> */}
                <TableCell>Date</TableCell>
                <TableCell>Regulator</TableCell>
                <TableCell>Rate</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {formData.map((data, index) => (
                <TableRow key={index}>
                  {/* <TableCell>{data.teamName}</TableCell> */}
                  <TableCell>{data.date}</TableCell>
                  <TableCell>{data.regulator}</TableCell>
                  <TableCell>{data.rate}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    </Container>
  );
}

export default CottonGrowthRegulationForm;

