import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  Button,
  Checkbox,
  Container,
  FormControl,
  FormControlLabel,
  FormGroup,
  Grid,
  InputAdornment,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography,
  Card,
  CardContent,
  Paper,
} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import HighlightOffIcon from "@mui/icons-material/HighlightOff";
import DoneIcon from "@mui/icons-material/Done";
import EditIcon from "@mui/icons-material/Edit";
import EditOffIcon from "@mui/icons-material/EditOff";
import { useApplication } from "./ApplicationContext";

function NitrogenManagementForm({ sectionData, hintText }) {
  // const [applicationType, setApplicationType] = useState("in-season");
  const [selectedCard, setSelectedCard] = useState(null);
  const [date, setDate] = useState("");
  const [amount, setAmount] = useState("");
  const [applications, setApplications] = useState([]);
  const teamName = sessionStorage.getItem("username");
  const [tableData, setTableData] = useState([]);
  const [subapplicationType, setSubApplicationType] = useState("None");
  // const [ApplicationType, setApplicationType] = useState("None");
  // const [isApplicationTypeConfirmed, setIsApplicationTypeConfirmed] =
  //   useState(false);
  const {
    applicationType,
    setApplicationType,
    isApplicationTypeConfirmed,
    setIsApplicationTypeConfirmed,
  } = useApplication();

  const handleApplicationTypeConfirmation = (event) => {
    setIsApplicationTypeConfirmed(event.target.checked);
  };

  const handleConfirmApplicationType = () => {
    setIsApplicationTypeConfirmed(true);
  };

  const handleCardClick = (value) => {
    setApplicationType(value);
  };

  const inseasonOptions = [
    "Broadcast",
    "Side-Dressed",
    "Liquidside-Dressing",
    "Fertigation",
  ];

  const controlledreleaseOptions = ["Broadcast", "Banding", "Incorporated"];

  const handleClick = (selected) => {
    if (selected === "Broadcast") setSubApplicationType("Broadcast");
    if (selected === "Banding") setSubApplicationType("banding");
    if (selected === "Incorporated") setSubApplicationType("incorporated");
    if (selected === "Side-Dressed") setSubApplicationType("side-dressed");
    if (selected === "Liquidside-Dressing")
      setSubApplicationType("liquidside-dressing");
    if (selected === "Fertigation") setSubApplicationType("fertigation");
    if (selectedCard === selected) {
      setSelectedCard(null);
      setSubApplicationType("None");
    } else setSelectedCard(selected);
  };

  useEffect(() => {
    // Fetch data from the backend when the component mounts
    fetchDataFromBackend();
  }, [applicationType]); // Fetch data whenever applicationType changes

  const handleDeleteApplication = (appId) => {
    axios
      .delete(`http://18.225.175.93:80/api/deletenitrogenApplication/${appId}`)
      .then((response) => {
        if (response.status === 200) {
          // Application deleted successfully, now update the table data
          fetchDataFromBackend();
        } else {
          console.error("Failed to delete the application");
        }
      })
      .catch((error) => {
        console.error("Error deleting the application:", error);
      });
  };

  const fetchDataFromBackend = () => {
    // Make an API request to fetch data from the backend based on the application type
    axios
      .get("http://18.225.175.93:80/api/fetchNitrogenManagementData", {
        params: {
          teamName,
          applicationType: applicationType,
        },
      })
      .then((response) => {
        if (response.status === 200) {
          // Set the retrieved data to the state variable
          setTableData(response.data);
        } else {
          console.error("Failed to fetch data from the backend");
        }
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
  };

  const handleAddApplication = () => {
    // Create a new application object
    let newApplication;

    if (applicationType === "in-season") {
      // Customize the object for in-season type
      newApplication = {
        applicationType: applicationType,
        date: date,
        amount: amount,
        placement: subapplicationType, // Initialize as an empty array
        teamName,
        applied: "no",
      };

      // Add placement options to the array if they are true
      // if (broadcast) newApplication.placement.push("Broadcast");
      // if (sideDressed) newApplication.placement.push("Side-dressed");
      // if (liquidSideDressing)
      //   newApplication.placement.push("Liquid Side-dressing");

      // // Convert the placement array to a comma-separated string
      // newApplication.placement = newApplication.placement.join(", ");
    } else if (applicationType === "controlled-release") {
      // Customize the object for controlled-release type
      newApplication = {
        applicationType: applicationType,
        // Add additional fields specific to controlled-release type
        // Example: controlledReleaseField: controlledReleaseValue,
        date: date,
        amount: amount,
        placement: subapplicationType,
        teamName,
        applied: "no",
      };
    }

    // Add the new application to the local state
    setApplications([...applications, newApplication]);

    // Clear the form fields
    setDate("");
    setAmount("");

    // Send the new application data to the backend
    sendApplicationDataToBackend(newApplication);
  };

  // Function to send application data to the backend using Axios
  const sendApplicationDataToBackend = (applicationData) => {
    axios
      .post(
        "http://18.225.175.93:80/api/nitrogenmanagementsubmit",
        applicationData
      )
      .then((response) => {
        if (response.status === 200) {
          console.log("Application data sent to the backend successfully");
          fetchDataFromBackend();
        } else {
          console.error("Failed to send application data to the backend");
        }
      })
      .catch((error) => {
        console.error("Error sending application data:", error);
      });
  };

  return (
    <Container maxWidth="100%">
      {/* <Typography variant="h6" gutterBottom>
        Add Application
      </Typography> */}
      {/* <div style={{ display: "flex", flexDirection: "row" }}>
        <FormControl component="fieldset">
          <RadioGroup
            aria-label="applicationType"
            name="applicationType"
            value={applicationType}
            onChange={(e) => setApplicationType(e.target.value)}
          >
            <FormControlLabel
              value="in-season"
              control={<Radio />}
              label="In-season Fertilizer Application"
            />
            <FormControlLabel
              value="controlled-release"
              control={<Radio />}
              label="Controlled-Release Fertilizer"
            />
          </RadioGroup>
        </FormControl>
      </div> */}

      <Grid container spacing={2}>
        <Grid item xs={6}>
          <Card
            onClick={() =>
              !isApplicationTypeConfirmed && handleCardClick("in-season")
            }
            sx={{
              cursor: !isApplicationTypeConfirmed ? "pointer" : "default",
              padding: "8px",
              backgroundColor:
                applicationType === "in-season" ? "#fa4616" : "#D8D4D7",
              border: "2px solid #fa4616",
              borderRadius: "8px",
            }}
          >
            <CardContent>
              <Typography variant="body2">
                In-season Fertilizer Application
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={6}>
          <Card
            onClick={() =>
              !isApplicationTypeConfirmed &&
              handleCardClick("controlled-release")
            }
            sx={{
              cursor: !isApplicationTypeConfirmed ? "pointer" : "default",
              padding: "8px",
              backgroundColor:
                applicationType === "controlled-release"
                  ? "#fa4616"
                  : "#D8D4D7",

              border: "2px solid #fa4616",
              borderRadius: "8px",
            }}
          >
            <CardContent>
              <Typography variant="body2">
                Controlled-Release Fertilizer
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
      <FormControlLabel
        control={
          <Checkbox
            checked={isApplicationTypeConfirmed}
            onChange={handleApplicationTypeConfirmation}
          />
        }
        label="Confirm application type (this cannot be changed later)"
      />

      {/* <Grid container spacing={2}>
        <Grid item xs={6}>
          <FormControl component="fieldset">
            <RadioGroup
              aria-label="applicationType"
              name="applicationType"
              value={applicationType}
              onChange={(e) => setApplicationType(e.target.value)}
            >
              <FormControlLabel
                value="in-season"
                control={<Radio />}
                label="In-season Fertilizer Application"
              />
            </RadioGroup>
          </FormControl>
        </Grid>
        <Grid item xs={6}>
          <FormControl component="fieldset">
            <RadioGroup
              aria-label="applicationType"
              name="applicationType"
              value={applicationType}
              onChange={(e) => setApplicationType(e.target.value)}
            >
              <FormControlLabel
                value="controlled-release"
                control={<Radio />}
                label="Controlled-Release Fertilizer"
              />
            </RadioGroup>
          </FormControl>
        </Grid>
      </Grid> */}

      {applicationType === "in-season" && (
        <>
          <p>
            The in-season application can occur using three methods called
            Banding, Bradcasting and Liquid side dressing After planting to V8,
            Dry application of ammonium nitrate in single or split applications
            can be applied via broadcast or side-dressed. From V8 to Harvesting
            liquid side dressing using the Miller Highboy “Y-drop” can be
            applied once each week on a fixed day (Thursday). Teams can choose
            the amount of UAN 28% (28-0-0-5) to apply (minimum of 24 lbs/ac N
            per application).
          </p>

          <p>
            In case of a leaching rain event (determined by the project
            management team), an additional application of N at 30 lbs/ac will
            be allowed. Participating teams will be responsible to communicate
            their decision at least three days in advance.
          </p>
          <Grid item xs={12}>
            <Typography variant="h5">In season application methods:</Typography>
            <div style={{ display: "flex", flexWrap: "wrap" }}>
              {inseasonOptions.map((option) => (
                <Card
                  key={option}
                  onClick={() => handleClick(option)}
                  sx={{
                    cursor: "pointer",
                    margin: "4px",
                    padding: "8px",
                    backgroundColor:
                      selectedCard === option ? "#fa4616" : "#D8D4D7",
                    border: "2px solid #fa4616",
                  }}
                >
                  <CardContent>
                    <Typography variant="body2">{option}</Typography>
                  </CardContent>
                </Card>
              ))}
            </div>
          </Grid>
          <br></br>
          <Grid item xs={12}>
            <FormControl variant="outlined" fullWidth>
              <InputLabel id="subapplicationtype-label">
                Application Type
              </InputLabel>
              <Select
                labelId="subapplicationtype-label"
                id="subapplicationtype"
                label="Application Type"
                value={subapplicationType}
                onChange={(e) => setSubApplicationType(e.target.value)}
              >
                <MenuItem value="None">None</MenuItem>
                <MenuItem value="Broadcast">Broadcast</MenuItem>
                <MenuItem value="side-dressed">Side-dressed</MenuItem>
                <MenuItem value="liquidside-dressing">
                  Liquid Side-dressing
                </MenuItem>
                <MenuItem value="fertigation">Fertigation</MenuItem>
              </Select>
            </FormControl>
          </Grid>
          <br></br>
        </>
      )}

      {applicationType === "controlled-release" && (
        <>
          <p>
            Controlled-Release Fertilizer Program, For the CRF program, you can
            choose any CRF rate ranging from 150 to 300 lbs/ac of N. All the CRF
            applications will be applied at planting.
          </p>

          <p>
            In case of a leaching rain event (determined by the project
            management team), an additional application of N at 30 lbs/ac will
            be allowed. Participating teams will be responsible to communicate
            their decision at least three days in advance.
          </p>
          <Grid item xs={12}>
            <Typography variant="h5">
              Controlled Release application methods:
            </Typography>
            <div style={{ display: "flex", flexWrap: "wrap" }}>
              {controlledreleaseOptions.map((option) => (
                <Card
                  key={option}
                  onClick={() => handleClick(option)}
                  sx={{
                    cursor: "pointer",
                    margin: "4px",
                    padding: "8px",
                    backgroundColor:
                      selectedCard === option ? "#fa4616" : "#D8D4D7",
                    border: "2px solid #fa4616",
                  }}
                >
                  <CardContent>
                    <Typography variant="body2">{option}</Typography>
                  </CardContent>
                </Card>
              ))}
            </div>
          </Grid>
          <br></br>
          <Grid item xs={12}>
            <FormControl variant="outlined" fullWidth>
              <InputLabel id="subapplicationtype-label">
                Application Type
              </InputLabel>
              <Select
                labelId="subapplicationtype-label"
                id="subapplicationtype"
                label="Application Type"
                value={subapplicationType}
                onChange={(e) => setSubApplicationType(e.target.value)}
              >
                <MenuItem value="None">None</MenuItem>
                <MenuItem value="Broadcast">Broadcast</MenuItem>
                <MenuItem value="banding">Banding</MenuItem>
                <MenuItem value="incorporated">Incorporated</MenuItem>
                {/* <MenuItem value="fertigation">Fertigation</MenuItem> */}
              </Select>
            </FormControl>
          </Grid>
          <br></br>
        </>
      )}
      <TextField
        id={`${sectionData}-date-input`}
        label="Date"
        variant="outlined"
        fullWidth
        type="date"
        value={date}
        onChange={(e) => setDate(e.target.value)}
        InputLabelProps={{
          shrink: true,
        }}
        // InputProps={{
        //   endAdornment: (
        //     <InputAdornment position="end">mm/dd/yy</InputAdornment>
        //   ),
        // }}
      />
      <br></br>
      <br></br>

      <TextField
        id={`${sectionData}-amount-input`}
        label="Amount (lbs/acre on N)"
        variant="outlined"
        fullWidth
        value={amount}
        onChange={(e) => setAmount(e.target.value)}
        helperText={hintText}
      />
      <p></p>
      <Button
        id={`n2-mgmnt-${sectionData}-add`}
        variant="contained"
        color="primary"
        onClick={handleAddApplication}
      >
        Add Application
      </Button>
      <Typography variant="h6" gutterBottom>
        Application Data
      </Typography>
      <TableContainer component={Paper}>
        <Table id={`table-n2-mgmnt-${sectionData}`}>
          <TableHead>
            <TableRow>
              <TableCell>#</TableCell>
              <TableCell>Date</TableCell>
              <TableCell>Amount</TableCell>
              <TableCell>placement</TableCell>
              <TableCell>Applied</TableCell>
              <TableCell>
                <EditIcon />
              </TableCell>
              {/* {applicationType === "in-season" && (
                <TableCell>placement</TableCell>
              )} */}
            </TableRow>
          </TableHead>
          <TableBody>
            {tableData.map((app, index) => (
              <TableRow key={app.id}>
                <TableCell>{index + 1}</TableCell>
                <TableCell>{app.date}</TableCell>
                <TableCell>{app.amount}</TableCell>
                <TableCell>{app.placement}</TableCell>
                <TableCell>
                  {app.applied === "no" ? <HighlightOffIcon /> : <DoneIcon />}
                </TableCell>
                <TableCell>
                  <button>
                    {app.applied === "no" ? (
                      <DeleteIcon
                        onClick={() => handleDeleteApplication(app.id)}
                      />
                    ) : (
                      <EditOffIcon />
                    )}
                  </button>
                </TableCell>

                {/*  {applicationType === "in-season" && (
                  <TableCell>{app.placement}</TableCell> 
                )}*/}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Container>
  );
}

export default NitrogenManagementForm;
