import React, { useState, useEffect } from "react";
import {
  Button,
  Container,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Typography,
  Card,
  CardContent,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import axios from "axios";

function CottonInsuranceSelectionForm() {
  const [coverage, setCoverage] = useState("none");
  const [level, setLevel] = useState("");
  const [formData, setFormData] = useState([]);
  const teamName = sessionStorage.getItem("username");

  const handleCardClick = (value) => {
    setCoverage(value);
  };

  const insuranceLevel = [
    "50%",
    "55%",
    "60%",
    "65%",
    "70%",
    "75%",
    "80%",
    "85%",
  ];

  const handleLevelCardClick = (selectedLevel) => {
    setLevel(selectedLevel);
  };

  // Fetch existing insurance selection data for the user
  const fetchInsuranceSelectionData = () => {
    axios
      .post("http://18.225.175.93:80/api/cottongetInsuranceSelectionForms", {
        username: teamName,
      })
      .then((response) => {
        if (response.status === 200) {
          setFormData(response.data);
        } else {
          console.error("Failed to fetch data from the backend");
        }
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
  };

  // Fetch existing insurance selection data for the user when the component mounts
  useEffect(() => {
    fetchInsuranceSelectionData();
  }, [teamName]);

  const handleSubmit = async () => {
    try {
      // Prepare the data to be sent
      const formDataToSubmit = {
        teamName: teamName,
        coverage: coverage,
        level: level,
      };

      // Send a POST request to your backend endpoint
      const response = await axios
        .post(
          "http://18.225.175.93:80/api/cottoninsurancesubmit",
          formDataToSubmit
        )
        .then((response) => {
          // Handle the successful response if needed
          if (response.status === 200) {
            console.log("Application data sent to the backend successfully");
            fetchInsuranceSelectionData();
          } else {
            console.error("Failed to send application data to the backend");
          }
        })
        .catch((error) => {
          // Handle errors
          console.error("Error submitting form:", error);
        });

      // Handle the successful response if needed

      // Fetch updated data after submission
    } catch (error) {
      // Handle errors
      console.error("Error submitting form:", error);
    }
  };

  return (
    <Container>
      <Typography variant="h6" gutterBottom>
        Insurance Selection
      </Typography>
      <div style={{ display: "flex" }}>
        {/* Cards for selecting contract type */}
        <Card
          onClick={() => handleCardClick("Yield protection")}
          sx={{
            cursor: "pointer",
            padding: "10px",
            backgroundColor:
              coverage === "Yield protection" ? "#fa4616" : "#D8D4D7",
            border: "2px solid #fa4616", // Highlight the selected card
            marginRight: "30px", // Add margin to separate cards
          }}
        >
          <CardContent>
            <Typography variant="body2">Yield protection</Typography>
          </CardContent>
        </Card>
        <Card
          onClick={() => handleCardClick("Revenue protection")}
          sx={{
            cursor: "pointer",
            padding: "10px",
            backgroundColor:
              coverage === "Revenue protection" ? "#fa4616" : "#D8D4D7",
            border: "2px solid #fa4616", // Highlight the selected card
          }}
        >
          <CardContent>
            <Typography variant="body2">Revenue protection</Typography>
          </CardContent>
        </Card>
      </div>
      <p></p>
      <div className="field">
        <div className="block control">
          <FormControl variant="outlined" fullWidth>
            <InputLabel htmlFor="select-insurance-coverage">
              Coverage
            </InputLabel>
            <Select
              id="select-insurance-coverage"
              name="coverage"
              label="Coverage"
              value={coverage}
              onChange={(e) => setCoverage(e.target.value)}
            >
              <MenuItem value="Yield protection">Yield protection</MenuItem>
              <MenuItem value="Revenue protection">Revenue protection</MenuItem>
            </Select>
          </FormControl>
        </div>
        <br />
        <div style={{ display: "flex", flexWrap: "wrap" }}>
          {/* Cards for selecting amount */}
          <Card
            sx={{
              cursor: "pointer",
              padding: "8px",
              backgroundColor: "white",
              marginBottom: "8px",
              marginRight: "2px",
              textAlign: "center",
              fontWeight: "bold",
              //border: "2px solid #fa4616",
            }}
          >
            <CardContent>
              <Typography variant="body2">Level</Typography>
            </CardContent>
          </Card>

          {insuranceLevel.map((option) => (
            <Card
              key={option}
              onClick={() => handleLevelCardClick(option)}
              sx={{
                cursor: "pointer",
                padding: "8px",
                backgroundColor: level === option ? "#fa4616" : "#D8D4D7",
                marginBottom: "8px",
                marginRight: "2px",
                textAlign: "center",
                fontWeight: "bold",
                border: "2px solid #fa4616",
              }}
            >
              <CardContent>
                <Typography variant="body2">{option}</Typography>
              </CardContent>
            </Card>
          ))}
        </div>
        <br></br>

        <div className="block control">
          <FormControl variant="outlined" fullWidth>
            <InputLabel htmlFor="select-insurance-coverage-level">
              Level
            </InputLabel>
            <Select
              id="select-insurance-coverage-level"
              name="level"
              label="Level"
              value={level}
              onChange={(e) => setLevel(e.target.value)}
            >
              <MenuItem value="50%">50%</MenuItem>
              <MenuItem value="55%">55%</MenuItem>
              <MenuItem value="65%">65%</MenuItem>
              <MenuItem value="70%">70%</MenuItem>
              <MenuItem value="75%">75%</MenuItem>
              <MenuItem value="80%">80%</MenuItem>
              <MenuItem value="85%">85%</MenuItem>
            </Select>
          </FormControl>
          <p></p>
        </div>
        <div className="columns">
          <div className="column">
            <div className="field">
              <div className="control">
                <Button
                  id="insurance-selection-submit"
                  variant="contained"
                  color="primary"
                  onClick={handleSubmit}
                >
                  Submit
                </Button>
              </div>
            </div>
          </div>
          <div className="column has-text-right">
            <div className="subtitle is-4" id="insurance-selection-status-msg">
              {/* Display status message here */}
            </div>
          </div>
        </div>
      </div>
      <div>
        <Typography variant="h6" gutterBottom>
          Existing Insurance Selection Data
        </Typography>
        <card>
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>#</TableCell>
                  <TableCell>Coverage</TableCell>
                  <TableCell>Level</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {formData.map((data, index) => (
                  <TableRow key={index}>
                    <TableCell>{index + 1}</TableCell>
                    <TableCell>{data.coverage}</TableCell>
                    <TableCell>{data.level}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </card>
        {/* <ul>
          {formData.map((data, index) => (
            <li key={index}>
              Coverage: {data.coverage}, Level: {data.level}
            </li>
          ))}
        </ul> */}
      </div>
    </Container>
  );
}

export default CottonInsuranceSelectionForm;
