import React, { useState } from "react";
import {
  TextField,
  Button,
  Container,
  Typography,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  CardActionArea,
  Card,
  CardContent,
} from "@mui/material";
import axios from "axios";

function AdminLoginForm() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [cropType, setCropType] = useState(""); // Default value: "corn"
  const [errorMessage, setErrorMessage] = useState("");

  const handleCardClick = (type) => {
    setCropType(type);
  };

  const cropOptions = [
    { label: "Corn", value: "corn" },
    { label: "Cotton", value: "cotton" },
    // Add more options here
  ];

  const handleSubmit = (e) => {
    e.preventDefault();

    // Prepare the data to send to the backend
    const formData = {
      username,
      password,
      cropType,
    };
    if (username === "admin" && password === "admin") {
      window.location.href = "/admin";
    }

    // Send form data to the backend using Axios
    axios
      .post("http://18.225.175.93:80/api/adminlogin", formData) // Replace with your backend URL
      .then((response) => {
        console.log("Response from server:", response.data);

        if (response.data.message === "Login successful") {
          // Store session variables in localStorage
          localStorage.setItem("username", username);
          localStorage.setItem("cropType", cropType);
          sessionStorage.setItem("username", username);
          sessionStorage.setItem("cropType", cropType);
          localStorage.setItem("accessToken", response.data.accessToken);
          sessionStorage.setItem("accessToken", response.data.accessToken);
          // Redirect to the welcome page
          window.location.href = "/adminspage";
          // if (cropType === "corn") {
          //   window.location.href = "/cornadmin";
          // } else if (cropType === "cotton") {
          //   window.location.href = "/cottonadmin";
          // }
        } else {
          setErrorMessage("Login failed. Invalid team name or password.");
        }
      })
      .catch((error) => {
        console.error("Error sending data:", error);
        // Handle error or show an error message
      });
  };

  return (
    <Container component="main" maxWidth="md">
      <div className="registration-container">
        <div className="registration-form">
          <Typography variant="h4" className="loginHeading">
            Login
          </Typography>
          {errorMessage && <p className="error-message">{errorMessage}</p>}
          <form onSubmit={handleSubmit}>
            <FormControl fullWidth>
              <InputLabel>Crop Type</InputLabel>
              <p></p>
              <div style={{ display: "flex", flexWrap: "wrap" }}>
                {cropOptions.map((option) => (
                  <Card
                    key={option.value}
                    onClick={() => handleCardClick(option.value)}
                    style={{ width: 200, margin: 10 }}
                    sx={{
                      cursor: "pointer",
                      padding: "10px",
                      backgroundColor:
                        cropType === option.value ? "#fa4616" : "#D8D4D7",
                      border: "2px solid #fa4616", // Highlight the selected card
                      marginRight: "30px", // Add margin to separate cards
                    }}
                  >
                    <CardContent>
                      <Typography gutterBottom variant="h5" component="div">
                        {option.label}
                      </Typography>
                    </CardContent>
                  </Card>
                ))}
              </div>
            </FormControl>
            <TextField
              label="Username"
              fullWidth
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              margin="normal"
              required
            />
            <TextField
              label="Password"
              type="password"
              fullWidth
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              margin="normal"
              required
            />

            <Button
              type="submit"
              variant="contained"
              color="secondary"
              fullWidth
            >
              Log In
            </Button>
          </form>
          <p></p>
          <p></p>

          {/* Use anchor tag for navigation */}
        </div>
      </div>
    </Container>
  );
}

export default AdminLoginForm;
