import React, { useState } from "react";
import {
  TextField,
  Button,
  Container,
  Typography,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  CardActionArea,
  Card,
  CardContent,
} from "@mui/material";
import axios from "axios";

function LoginForm() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [cropType, setCropType] = useState("corn"); // Default value: "corn"
  const [errorMessage, setErrorMessage] = useState("");

  const handleCardClick = (type) => {
    setCropType(type);
  };

  const cropOptions = [
    { label: "Corn", value: "corn" },
    { label: "Cotton", value: "cotton" },
    // Add more options here
  ];

  const handleSubmit = (e) => {
    e.preventDefault();

    // Prepare the data to send to the backend
    const formData = {
      username,
      password,
      cropType,
    };
    if (username === "admin" && password === "admin") {
      window.location.href = "/admin";
    }

    // Send form data to the backend using Axios
    axios
      .post("http://18.225.175.93:80/api/login", formData) // Replace with your backend URL
      .then((response) => {
        console.log("Response from server:", response.data);

        if (response.data.message === "Login successful") {
          // Store session variables in localStorage
          localStorage.setItem("username", response.data.user.teamName);
          localStorage.setItem("cropType", response.data.cropType);
          sessionStorage.setItem("username", response.data.user.teamName);
          sessionStorage.setItem("cropType", response.data.cropType);

          // Redirect to the welcome page
          if (cropType === "corn") {
            window.location.href = "/welcome";
          } else if (cropType === "cotton") {
            window.location.href = "/welcomecotton";
          }
        } else {
          setErrorMessage("Login failed. Invalid team name or password.");
        }
      })
      .catch((error) => {
        console.error("Error sending data:", error);
        // Handle error or show an error message
      });
  };

  return (
    <Container component="main" maxWidth="md">
      <div className="registration-container">
        <div className="registration-form">
          <Typography variant="h4" className="loginHeading">
            Login
          </Typography>
          {errorMessage && <p className="error-message">{errorMessage}</p>}
          <form onSubmit={handleSubmit}>
            <FormControl fullWidth>
              <InputLabel>Crop Type</InputLabel>
              {/* <Select
                label="Crop Type"
                fullWidth
                value={cropType}
                onChange={(e) => setCropType(e.target.value)}
                margin="normal"
                required
              >
                <MenuItem value="corn">Corn</MenuItem>
                <MenuItem value="cotton">Cotton</MenuItem>
              </Select> */}
              <p></p>
              <div style={{ display: "flex", flexWrap: "wrap" }}>
                {cropOptions.map((option) => (
                  <Card
                    key={option.value}
                    onClick={() => handleCardClick(option.value)}
                    style={{ width: 200, margin: 10 }}
                    sx={{
                      cursor: "pointer",
                      padding: "10px",
                      backgroundColor:
                        cropType === option.value ? "#fa4616" : "#D8D4D7",
                      border: "2px solid #fa4616", // Highlight the selected card
                      marginRight: "30px", // Add margin to separate cards
                    }}
                  >
                    <CardContent>
                      <Typography gutterBottom variant="h5" component="div">
                        {option.label}
                      </Typography>
                    </CardContent>
                  </Card>
                ))}
              </div>
            </FormControl>
            <TextField
              label="Username"
              fullWidth
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              margin="normal"
              required
            />
            <TextField
              label="Password"
              type="password"
              fullWidth
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              margin="normal"
              required
            />

            <Button
              type="submit"
              variant="contained"
              color="secondary"
              fullWidth
            >
              Log In
            </Button>
          </form>
          <p></p>
          {/* <p>
            Not registered yet?{" "}
            <a href="/register" className="signUpLink">
              Sign up
            </a>{" "}
          </p> */}
          <div style={{ display: "flex", alignItems: "center" }}>
            <p style={{ margin: 0, marginRight: "10px" }}>
              Not registered yet?
            </p>
            {/* <a href="/register" className="signUpLink">Sign up</a> */}
            <button
              variant="outlined"
              className="signUpButton"
              size="small"
              onClick={() => (window.location.href = "/register")}
            >
              Sign up
            </button>
          </div>
          <p></p>
          <p>
            Admin login page :{" "}
            <a href="/adminlogin" className="signUpLink">
              Admin login
            </a>
          </p>
        </div>
      </div>
    </Container>
  );
}

export default LoginForm;
