import React, { useState, useEffect } from "react";
import axios from "axios";
import { jwtDecode } from "jwt-decode";
import { Paper, Typography, Button } from "@mui/material";
import { useNavigate } from "react-router-dom";

function AdminProfile() {
  const [adminDetails, setAdminDetails] = useState(null);
  let navigate = useNavigate();

  useEffect(() => {
    const accessToken = sessionStorage.getItem("accessToken");
    if (accessToken) {
      const decoded = jwtDecode(accessToken);
      const adminId = decoded.id; // Replace 'id' with the actual key used in your token

      axios
        .get(`http://18.225.175.93:80/api/adminDetails?adminId=${adminId}`)
        .then((response) => {
          setAdminDetails(response.data);
        })
        .catch((error) => {
          console.error("Error fetching admin details:", error);
        });
    }
  }, []);

  const handleHomeClick = () => {
    navigate("/adminspage");
  };

  if (!adminDetails) {
    return <div>Loading...</div>;
  }

  return (
    <div>
      <Paper
        elevation={3}
        style={{
          padding: 20,
          margin: 20,
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography variant="h5" gutterBottom>
          Welcome, {adminDetails.username}!
        </Typography>
        <Button variant="contained" color="primary" onClick={handleHomeClick}>
          Admin Home
        </Button>
      </Paper>
      <Paper
        elevation={2}
        style={{
          padding: 20,
          margin: 20,
        }}
      >
        <h1>Admin Details</h1>
        <p></p>
        <p>Username: {adminDetails.username}</p>
        <p>Username: {adminDetails.email}</p>
        <p>Username: {adminDetails.phone}</p>
        {/* Display other admin details */}
      </Paper>
    </div>
  );
}

export default AdminProfile;
