import React, { useState, useEffect } from "react";
import {
  TextField,
  Button,
  Typography,
  TextareaAutosize,
  Grid,
  Container,
  Card, // Import Card from Material-UI
  CardContent, // Import CardContent from Material-UI
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
} from "@mui/material";
import axios from "axios"; // Import Axios for making HTTP requests

function CottonHybridForm() {
  const [hybrid, setHybrid] = useState("");
  const [cost, setCost] = useState("");
  const [notes, setNotes] = useState("");
  const [statusMsg, setStatusMsg] = useState("");
  const teamName = sessionStorage.getItem("username");
  const [submittedForms, setSubmittedForms] = useState([]);

  const hybridOptions = [
    "DP2038",
    "DP2020",
    "NG4936",
    "PHY411",
    "Armor Seed 9371",
    "Others",
  ];

  const handleHybridClick = (selectedHybrid) => {
    setHybrid(selectedHybrid);
  };

  const fetchSubmittedForms = async () => {
    try {
      const username = sessionStorage.getItem("username");
      const response = await axios.post(
        "http://18.225.175.93:80/api/getCottonHybridSubmittedForms",
        { username } // Send username as part of the request body
      );

      if (response.status === 200) {
        // Update the state with the fetched data
        setSubmittedForms(response.data);
      } else {
        console.error("Failed to fetch submitted forms data");
      }
    } catch (error) {
      console.error("Error fetching submitted forms data:", error);
    }
  };

  useEffect(() => {
    // Fetch the submitted forms data for the logged-in user
    // Call the fetch function when the component mounts
    fetchSubmittedForms();
  }, []);

  const handleFormSubmit = async (e) => {
    e.preventDefault();

    // Prepare the data to be sent to the backend
    const formData = {
      hybrid,
      cost,
      notes,
      teamName,
    };

    try {
      // Make an HTTP POST request to your backend API using Axios
      const response = await axios.post(
        "http://18.225.175.93:80/api/cottonhybridsubmit", // Replace with your actual backend URL
        formData
      );

      if (response.status === 200) {
        // Form submission was successful
        fetchSubmittedForms();
        setStatusMsg("Form submitted successfully");
      } else {
        // Form submission failed
        setStatusMsg("Form submission failed");
      }
    } catch (error) {
      console.error("Error submitting the form:", error);
      setStatusMsg("Form submission failed");
    }
  };

  return (
    <Container>
      <Grid item xs={12}>
        <Typography variant="h5">Hybrids to use:</Typography>
        <div style={{ display: "flex", flexWrap: "wrap" }}>
          {hybridOptions.map((option) => (
            <Card
              key={option}
              onClick={() => handleHybridClick(option)}
              sx={{
                cursor: "pointer",
                margin: "4px",
                padding: "8px",
                backgroundColor: hybrid === option ? "#fa4616" : "#D8D4D7", // Highlight the selected card
                border: "2px solid #fa4616", // Border for all cards
                borderRadius: "8px", // Rounded corners
                color: hybrid === option ? "white" : "black",
              }}
            >
              <CardContent>
                <Typography variant="body2">{option}</Typography>
              </CardContent>
            </Card>
          ))}
        </div>
      </Grid>
      <Typography variant="h4" gutterBottom>
        My Form
      </Typography>
      <form onSubmit={handleFormSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <TextField
              id="hybrid-input"
              label="Hybrid"
              variant="outlined"
              fullWidth
              value={hybrid}
              onChange={(e) => setHybrid(e.target.value)}
              placeholder="Hybrid"
              required
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              id="cost-input"
              label="Cost"
              variant="outlined"
              fullWidth
              value={cost}
              onChange={(e) => setCost(e.target.value)}
              placeholder="Cost"
              required
            />
            <Typography variant="body2" color="textSecondary">
              $/bag
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <TextField
              id="notes-input"
              label="Notes"
              variant="outlined"
              fullWidth
              multiline
              rows={4}
              value={notes}
              onChange={(e) => setNotes(e.target.value)}
              placeholder="Notes"
            />
          </Grid>
          <Grid item xs={12}>
            <Button
              id="hybrid-selection-submit"
              variant="contained"
              color="primary"
              type="submit"
            >
              Submit
            </Button>
          </Grid>
          <Grid item xs={12} sx={{ textAlign: "right" }}>
            <Typography variant="subtitle1" id="hybrid-status-msg">
              {statusMsg}
            </Typography>
          </Grid>
        </Grid>
      </form>
      {/* {submittedForms.length > 0 && (
        <div>
          <Typography variant="h5" gutterBottom>
            Submitted Data
          </Typography>
          {submittedForms.map((form, index) => (
            <Card key={index} sx={{ marginBottom: 2 }}>
              <CardContent>
                <Typography variant="h6">Hybrid: {form.hybrid}</Typography>
                <Typography variant="body1">
                  Cost: ${form.cost} per bag
                </Typography>
                <Typography variant="body2">Notes: {form.notes}</Typography>
                <Typography variant="body2">
                  Team Name: {form.teamName}
                </Typography>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </Container>
  ); */}
      {submittedForms.length > 0 && (
        <div>
          <strong>
            {" "}
            <Typography variant="h5" gutterBottom>
              Submitted Data
              <br></br>
            </Typography>{" "}
          </strong>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  <b>Hybrid</b>
                </TableCell>
                <TableCell>
                  <b>Cost per Bag</b>
                </TableCell>
                <TableCell>
                  <b>Notes</b>
                </TableCell>
                {/* <TableCell>
                    <b>Team Name</b>
                  </TableCell> */}
              </TableRow>
            </TableHead>
            <TableBody>
              {submittedForms.map((form, index) => (
                <TableRow key={index}>
                  <TableCell>{form.hybrid}</TableCell>
                  <TableCell>${form.cost}</TableCell>
                  <TableCell>{form.notes}</TableCell>
                  {/* <TableCell>{form.teamName}</TableCell> */}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      )}
    </Container>
  );
}

export default CottonHybridForm;
