import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  Tabs,
  Tab,
  Paper,
  TextField,
  Button,
  Typography,
  TextareaAutosize,
  Grid,
  Container,
  List,
  Link,
  ListItemText,
  ListItem,
  Card, // Import Card from Material-UI
  CardContent, // Import CardContent from Material-UI
} from "@mui/material";

function CornBulletin() {
  const [files, setFiles] = useState([]);
  const [teamfiles, setTeamFiles] = useState([]);
  const [fileInput, setFileInput] = useState(null);
  const teamName = sessionStorage.getItem("username");

  useEffect(() => {
    const fetchFiles = async () => {
      try {
        const response = await axios.get(
          `http://18.225.175.93:80/api/listTeamFiles/${teamName}`
        );
        setTeamFiles(response.data.files);
      } catch (error) {
        console.error("Error fetching files:", error);
        // Handle error appropriately
      }
    };
    fetchFiles();
  }, []);

  useEffect(() => {
    // Fetch the list of files when the component mounts
    axios
      .get("http://18.225.175.93:80/api/listFiles")
      .then((response) => {
        if (response.status === 200) {
          setFiles(response.data.files);
        } else {
          console.error("Failed to fetch files from the backend");
        }
      })
      .catch((error) => {
        console.error("Error fetching files:", error);
      });
  }, []);
  return (
    // <div>
    //   <h3>List of Files in Uploads Folder:</h3>
    //   <ul>
    //     {files.map((fileName, index) => (
    //       <li key={index}>
    //         {fileName}{" "}
    //         <a
    //           href={`http://18.225.175.93:80/api/downloadFile/${fileName}`}
    //           download
    //         >
    //           Download
    //         </a>
    //       </li>
    //     ))}
    //   </ul>
    // </div>
    <Container maxWidth="sm">
      <Paper elevation={3} style={{ padding: "20px" }}>
        <Typography variant="h5" gutterBottom>
          List of Files in Uploads Folder:
        </Typography>
        <List>
          {files.map((fileName, index) => (
            <ListItem key={index}>
              <ListItemText primary={fileName} />
              <Link
                href={`http://18.225.175.93:80/api/downloadFile/${fileName}`}
                download
              >
                Download
              </Link>
            </ListItem>
          ))}
        </List>
        <List>
          {teamfiles.map((fileName, index) => (
            <ListItem key={index}>
              <ListItemText primary={fileName} />
              <Link
                href={`http://18.225.175.93:80/api/downloadTeamFile/${teamName}/${fileName}`}
                download
              >
                Download
              </Link>
            </ListItem>
          ))}
        </List>
      </Paper>
    </Container>
  );
}
export default CornBulletin;
